.prevention-section {
   margin: 0px 100px;
   padding-top: 100px;
}
.prevention-container {
   background: #3d1bb6;
   border-radius: 10px;
}
.prevention-title {
   font-size: 24px;
   line-height: 28px;
   text-align: center;
   color: #ffffff;
   padding: 8px;
}
.prevention-text {
   font-size: 36px;
   line-height: 42px;
   text-align: center;
   color: #23ae41;
}
.prevention-dock {
   display: flex;
   flex-flow: row wrap;
   justify-content: space-around;
}
