body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Roboto Condensed', sans-serif;
    overflow-x: hidden;
    scroll-behavior: smooth;
}
.dashboard-container {
   margin: 100px;
   padding: 40px 0px;
   display: flex;
   flex-flow: row wrap;
   justify-content: space-around;
   align-items: center;
   background: rgba(206, 47, 3, 1);
   border-radius: 10px;
   font-family: 'Roboto Condensed', sans-serif;
}
.dashboard-heading {
   font-weight: bold;
   font-size: 24px;
   line-height: 28px;
   letter-spacing: 0.05em;
   color: #ffffff;
   text-align: center;
}
.dashboard-dock {
   display: flex;
   flex-flow: row nowrap;
}

@media (max-width: 768px) {
   .dashboard-container{
      margin: 50px;
      padding: 0;
   }
   .dashboard-heading {
      font-size: 16px;
      font-weight: bold;
   }
}
@media (max-width: 426px) {
   .dashboard-container{
      margin: 50px 20px;
   }
   .dashboard-dock{
      flex-flow: column;
   }
}

.dashboard-card{
    width: 200px;
    height: 100px;
    border-radius: 10px;
    margin: 8px 4px;
}
.dashboard-card .card-body{
    font-size: 24px;
    line-height: 23px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    letter-spacing: 0.05em;
 }

 /* Tablet View */
 @media (max-width: 768px){
    .dashboard-card{
        width: 145px;
        border-radius: 10px;
    }
    .dashboard-card .card-body{
        font-size: 16px;
        line-height: 23px;
        letter-spacing: 0.05em;
     }
     .dashboard-card .card-body .card-title,.dashboard-card .card-body .card-text{
         font-size: 16px;
     }
 }
 /* Mobile View */
 @media (max-width: 426px){
    .dashboard-card{
        width: 100%;
        margin: 4px;
    }
    .dashboard-card .card-body{
        font-size: 16px;
        line-height: 23px;
        letter-spacing: 0.05em;
     }
 }
.hero {
   background: linear-gradient(101.82deg, #f7b733 -1.91%, #fc4a1a 102.01%);
   padding: 100px;
}
.hero-text {
   color: white;
   font-family: 'Roboto Condensed', sans-serif;
   letter-spacing: -1.5%;
}
.hero-text .text {
   padding-bottom: 16px;
}
.medium-text {
   font-size: 24px;
   font-weight: 700;
   line-height: 28px;
}
.large-text {
   font-size: 96px;
   font-weight: 700;
   line-height: 112px;
}
.paragraph {
   font-size: 24px;
   font-weight: 400;
   line-height: 32px;
}
.track-btn {
   width: -webkit-fit-content;
   width: -moz-fit-content;
   width: fit-content;
   padding: 10px 24px;
   text-align: center;
   background: #ce2f03;
   box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
   border-radius: 10px;
}
.hero .row .hero-img {
   display: flex;
   justify-content: flex-end;
}
.hero-image {
   width: 400px;
   height: 400px;
}

/* Tablet View */
@media (max-width: 768px) {
   .hero {
      padding: 50px;
   }
   .hero-text .text {
      padding-bottom: 8px;
   }
   .medium-text {
      font-size: 16px;
      line-height: 20px;
   }
   .large-text {
      font-size: 64px;
      line-height: 70px;
   }
   .paragraph {
      font-size: 16px;
      line-height: 24px;
   }
   .hero-image {
      width: 300px;
      height: 300px;
   }
}

/* Mobile View */
@media (max-width: 426px) {
   .hero {
      padding: 80px 20px 20px 20px;
   }
   .hero-text .text {
      padding-bottom: 4px;
   }
   .large-text {
      font-size: 32px;
   }
   .hero .row .hero-img {
      display: flex;
      justify-content: center;
   } 
   .hero-image {
      width: 200px;
      height: 200px;
   }
}

